import React from "react";
import { FormContainer, FoodInput, Button, Select } from "./HeaderStyles";

const Form = ({ setQuery, getData, mealTypes, setMeal }) => {
  const handleSubmit = (e) => {
    e.preventDefault();
    //  Normal artlarda search linkine tklandnda, form gnderilir, (breakfast tklanm gibi). biz aadaki ekilde bir yap kullanrsak ve tklanma olaynda e.preventDefault(); dersek  nesnenin o anki ilevini iptal edip devamnda kod varsa ilemeye devam edecektir alttaki rneimizde grld gibi e.preventDefault() satrndan sonra ki kod satrlarna geer
    getData();

    // mantken buraya yazlmal, butona da yazarsak  api yi 2 kere arm oluyor. console da buras akken bak
  };

  return (
    /* 3 arama kutusunun topland kutu */
    <FormContainer onSubmit={handleSubmit}>
      <FoodInput
        type="text"
        placeholder="Search"
        onChange={(e) => setQuery(e.target.value)}
      />
      {/* arama yaplacak rnn yazld input */}
      <Button
        type="submit"

        // onClick={getData} yazarsak 2 kere veri getirir. zaten bu buton submit ilemi yapacak ve form un onsubmit ini tetikleyecek
      >
        Search
      </Button>
      {/* ya butona tklaynca getdata altrsn ya form u handlesubmit e yolla, butonu handlesubmit e yollasanda olur */}
      <Select
        name="mealTypes"
        id="mealTypes"
        onChange={(e) => setMeal(e.target.value)}
      >
        {/* onChange ile setMeal e attm option la browser da gstereceiz */}
        {mealTypes.map((item, index) => (
          <option key={index} value={item.toLowerCase()}>
            {item}
          </option>
        ))}
      </Select>
    </FormContainer>
  );
};

export default Form;