import { Navigate, Outlet } from "react-router-dom";

const PrivateRouter = () => {
  const user = JSON.parse(sessionStorage.getItem("user"));
  //sessionStorage a gmlm user varsa getir, varsa ierde hangi sayfa denildiyse oraya git, yoksa login
  return user ? <Outlet /> : <Navigate to="/" />;
};
//  <Outlet />; = Route nin iindeki dndrlecek component, child
export default PrivateRouter;
