import React, { useState } from "react";
import { Nav, Logo, Hamburger, MenuLink, Menu } from "./NavbarStyles";
//  import { ExternalLink } from "react-external-link";
// import { GiHamburgerMenu } from "react-icons/gi";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [github, setGithub] = useState("");
  return (
    <Nav>
      <Logo to="/home">
        <i>{"<Chef Enes'>"}</i>
        <span> Recipes</span>
      </Logo>
      <Hamburger onClick={() => setIsOpen(!isOpen)}>
        {/* hamburger o anda ne durumdaysa, tklandnda tersi olacak, false ise true..ve sonra Menu bunu style a props la gnder */}
        {/* <GiHamburgerMenu /> */}
        <span />
        <span />
        <span />
        {/* XHTML= HTML ile hazrlanm sayfalarn tm cihazlarda dzgn almas iin gelitirilmitir. Bu yzden <span></span> eklinde yazm (HTML), dinamik stillerin dzgn bir ekilde oluturulmamasna,
        yeniden dzenleme ilemi srasnda hatalara, eksik bir tema desteine
        neden olabilir ve uygulamanz iyi bir sebep olmadan daha byk hale
        getirebilir. */}
        {/* bo spanlar verilen stillerle izgi yapyor */}
      </Hamburger>

      <Menu osman={isOpen}>
        {/* buradan styles sayfasna true false durumu gidiyor,5. satrda ilk false (kapal) balyor, tklandka setIsOpen ile deiiyor */}
        <MenuLink onClick={() => setIsOpen(!isOpen)} to="/about">
          About
        </MenuLink>
        {/* hamburger tklannca open alyor, about a basnca false kapanmal */}
        <MenuLink
          onClick={() =>
            setGithub(
              (window.location.href =
                "https://github.com/enes9103")
            )
          }
          to={github}
          target="_blank"
        >
          Github
        </MenuLink>
        {/* React-router, Tek Sayfalk Uygulama iin bir ynlendirme sistemidir. Tm
        rotalarn sayfanzda tanmlanmas ve uygulamanzn iinde kapsllenmesi
        gerekir */}
        {/* <ExtrnlLink href="https://github.com/enes9103">
          Github
        </ExtrnlLink> */}
        {/* alttaki gibi stil sayfasnda a tag inden tremi A ile de bu ii yapabiliriz */}
        {/* <A href="https://github.com" target="_blank">
          Github
        </A> */}
        <MenuLink
          onClick={() => setIsOpen(!isOpen)}
          // onMouseUp= fare dmesini brakrken
          onMouseUp={() => sessionStorage.clear()}
          to="/"
        >
          Logout
        </MenuLink>
        {/* sessionStorage.clear()=> bunu yazmazsak sessionStorage temizlenmiyor, logout yapnca direk girilebiliyor */}
      </Menu>
    </Nav>
  );
};

export default Navbar;