import styled from "styled-components";
import { Link } from "react-router-dom";
// import { ExternalLink } from "react-external-link";
export const Nav = styled.div`
  /* ak yeil olan navbar */
  padding: 0 2rem;
  display: flex;
  justify-content: space-between; //yatayda aralkl objeler halinde ortala
  align-items: center; // dikeyde ortala
  flex-wrap: wrap;
  /* Varsaylan olarak esnek eler tek bir satra smaya alrlar. Gerektiinde birden fazla satra yaymak iin bu zellii kullanabilirsiniz */
  background: #e1f1dd;
  border-radius: 0 0 10px 10px;
  height: 75px;
  font-size: 2rem;
`;
export const Hamburger = styled.div`
  display: none;
  /* Bir eyi gizleme zellii  e gizlenecek ve sayfa, e orada deilmi gibi alttaki stiller yok gibi grntlenecektir: */
  flex-direction: column;
  /*izgiler stste hamburger gibi */
  cursor: pointer;
  span {
    height: 4px;
    width: 25px;
    background: #00adb5;
    /* margin-left: 250px; */
    margin-bottom: 4px;
    border-radius: 5px;
  }
  @media (max-width: 768px) /* ekran 768 den klnce hamburger ekli gzksn */ {
    display: flex;
    /* kapsayc ierisindeki elere artk esneklik vermek iin, artk gzkmesi iin*/
  }
`;

export const MenuLink = styled(Link)`
  /* navbardaki 3 kelime ... tek tek about vs yazanlarn zellikleri */
  padding: 1rem 2rem;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  /* kelimelerin alt izili olmasn */
  color: #02475e;
  
  /* transition: all 0.3s ease-in; */
  font-size: 2rem;
  font-family: "Girassol", sans-serif;
  &:hover {
    color: #00adb5;
    font-weight: bold;
  }
  @media (max-width: 768px) {
    /* hamburger meydana ktnda (tklannca alan merdiven, Menu deki lyle ayn olmal) */
    /* ekran klnce alttaki stiller olsun */
    border: 1px solid #00adb5;
    border-radius: 10px;
    width: 91%;
  }
`;
export const Menu = styled.div`
  /* navbardaki 3 kelime about ...hepsini iine alan sarmaln (kutunun ) zellikleri*/
background: #e1f1dd;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  /* dier stillere gre ayarla kendini, bunu yazmazsak food app in altnda kalacak */
  /* 3 kelime  alnca stteki zelliklerde olsun*/
  @media (max-width: 768px) {
    overflow: hidden;
    /* uzun yaz olurda taarsa gizle */
    flex-direction: column;
    width: 100%;
    /* gap:2px; */
    /* props olarak, osman=true geldi mesela */
    max-height:${({osman})=>(osman?"300px":"0")}
    /* 3 izgiye ilk tklandnda 300 px alsn, sonraki tklamada kapansn */
    /* max-height:300px yazarsak hep ak olur */
    /* transition: max-height 0.3s ease-in; */
  }
`;
export const Logo = styled(Link)`
  padding: 1rem 0;
  color: #393e46;
  text-decoration: none;
  font-weight: 800;
  font-size: 2rem;
  i {
    font-family: "Girassol", sans-serif;
  }
  span {
    font-family: "Girassol", sans-serif;
    font-weight: 400;
    font-size: 2rem;
    color: #00adb5;
  }
`;
