
import axios from 'axios';
import React, {useState} from 'react'
import Header from "../../components/header/Header";
import { ImgDiv, MainContainer,HomeImg } from './HomeStyles';
import homeSvg from '../../assets/home.svg'
import RecipeCardComp from "./RecipeCardComp"

// a1e0a190
const APP_ID = "a1e0a190"; 
const APP_KEY = "2117f8b024066fcb2fe0b9ce23c8ef60";
/****buraya kendi id ve key imizi yaziyoruz**********/


const Home = () => {
  const [query, setQuery] = useState("");
  const [food, setFood] = useState();
  const mealTypes = ["Breakfast", "Lunch", "Dinner", "Snack", "Teatime"];
  const [meal, setMeal] = useState(mealTypes[0].toLowerCase());

  // query=yazdmz sorgu kelimesi, meal=breakfast vs
  const url = `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}&mealType=${meal}`;

  //   Async/await i daha gzel yapan ancak ilevsellikte hibir deiiklii olmayan bir kod bkmdr.crud ilemlerini kolaylatrr. Birka iyi nedenden dolay sz zinciri (then) yerine async/await kullanlmas nerilir:
  // Mevcut i paracna zarar vermez,
  // Sz zincirleme ile karlatrldnda CPU'da fazla maliyeti yoktur ,
  // Senkronizeymi gibi kod yazmanza yardmc olur,

  // await tek bana kullanlmas bir szdizimi hatas retecektir. Bu nedenle, async ilevinin iinde await kullann,
  //url ye balandnzda then ile devam ederseniz ok, ama ksaca axios kullanacaksak, veri ekerken beklemeyip alt satra geer, o yzden bekle await diyoruz
  const getData = async () => {
    if (query) {
      const result = await axios.get(url);
      setFood(result.data.hits);
      console.log(result.data.hits);
      //sadece result  yazdrrsak bir sr yabanc ifade gelecek, bizim esas istediimiz data dizisi, result n bir alt esi, dataya ulamak iin result.data
      // data nn iinde hits ve more key i var, hits =btn dizi toplu halde json olarak, 10 tane pie mesela. data 1 den 10 a kadar gster diyor, (daha fazla varsa bile ) more =true
    } else {
      console.log("please fill the form");
    }
  };

  return (
    <div>
      {/* alttakileri header a oradan da form a gnderiyorum, orada setMeal ve setQuery alacak setter a gnderiyorum yani */}
      {/* componente gidecekse tek kelime, style a gidecekse a kapa 2 kelime */}
      <Header
        setQuery={setQuery}
        getData={getData}
        mealTypes={mealTypes}
        setMeal={setMeal}
        // meal={meal}
        //meal = breakfast mesela. mealTypes= btn hepsi, breakfast, lunch....
      />

      {food ? (
        // api den gelen datann tm, (bir yiyecek ismi girildiinde)
        <MainContainer>
          {/* arama sonucu yiyecek kartlarnn kutusu */}
          {food.map((liste, index) => (
            <RecipeCardComp key={index} recipe1={liste.recipe} />
            // sadece liste yollarsak ap deki gibi  gidiyor. liste.recipe= json formatnda key value. ksaca{recipe}.hits in iinde recipe var, liste hits e kadar gelir ben iindeki recipe'yi istiyorum
            // veriler direk bu sayfaya geliyor ,RecipeCardComp deki gibi navigate state e gerek yok
          ))}
        </MainContainer>
      ) : (
        <ImgDiv>
          <HomeImg src={homeSvg} />

          {/* ekrandaki a resmi */}
        </ImgDiv>
      )}
    </div>
  );
}

export default Home